.contactForm {
    display: flex;
    // height: 100vh;
    flex-direction: column;
    background: linear-gradient(to bottom, var(--color-jantar), var(--color-vinova));
}

.contactFormComponent {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 30px;
    justify-content: center;
    display: flex;
    flex-direction: column;
    width: 50%;
    position: relative;
